.contactInfo{padding:24px;border-radius:18px;border:1px solid #eae9f2;background:#FBFBFE;box-shadow:0px 6px 15px 0px rgba(0,0,0,0.04);display:flex;flex-direction:column;gap:40px}.contactInfo .headerContact{display:flex;justify-content:space-between;align-items:center}.contactInfo .headerContact .headerContactLeft{gap:3px;display:flex;align-items:center}.contactInfo .headerContact .headerContactLeft .profileImgContainer{width:80px;height:80px;display:grid;place-items:center}.contactInfo .headerContact .headerContactLeft .profileImgContainer .profileImg{width:62px;height:62px;border-radius:12px}.contactInfo .headerContact .headerContactLeft .profileTitles{display:flex;flex-direction:column}.contactInfo .headerContact .headerContactLeft .profileTitles .profileTitle{font-size:16px;font-weight:600;color:#544CA6}.contactInfo .headerContact .headerContactLeft .profileTitles .profileSubTitle{font-size:12px;font-weight:400;color:#A3A3A4}.contactInfo .headerContact .headerContactRight{display:flex;gap:12px}.contactInfo .detailsContact{padding:16px 4px;display:flex;flex-direction:column;gap:20px}.contactInfo .detailsContact .textWithLabel{display:flex}.contactInfo .detailsContact .textWithLabel .textLabel{font-weight:600;font-size:12px;line-height:15px;color:#A4AAB8;width:40%;display:flex;align-items:center}.contactInfo .detailsContact .textWithLabel .textValue{font-weight:600;font-size:14px;line-height:18px;color:#323759;width:60%;display:flex;align-items:center}
