.titleWrapper{display:flex;justify-content:space-between;align-items:flex-end;margin-bottom:17px}.title{font-weight:700;font-size:17px;color:#544CA6}.pagingText{font-weight:600;font-size:10px;color:#8492A6}.row{display:flex;margin-bottom:8px}.name{width:40%;font-weight:bold;font-size:11px;color:#A3A3A4}.value{width:60%;font-weight:600;font-size:15px;color:#333333}.text{font-weight:400;font-size:14px;margin-left:5px;color:#333333}.paragraph{font-weight:400;font-size:14px;color:#333333}.goldContainer{width:100%;height:0;position:relative;padding-bottom:56.25%}.carouselWrapper{position:absolute;width:100%;height:100%;border-radius:6px;overflow:hidden}.carouselWrapperFlex{height:100%;display:flex;position:relative;transition:all 1s}.carouselWrapperFlex[data-active-carousel="0"]{left:0}.carouselWrapperFlex[data-active-carousel="-1"]{left:-100%}.carouselWrapperFlex[data-active-carousel="-2"]{left:-200%}.carouselWrapperFlex[data-active-carousel="-3"]{left:-300%}.carouselWrapperFlex[data-active-carousel="-4"]{left:-400%}.carouselWrapperFlex[data-active-carousel="-5"]{left:-500%}.carouselWrapperFlex[data-active-carousel="-6"]{left:-600%}.carouselWrapperFlex[data-active-carousel="-7"]{left:-700%}.carouselWrapperFlex[data-active-carousel="-8"]{left:-800%}.carouselWrapperFlex[data-active-carousel="-9"]{left:-900%}.img{width:100%;height:100%;object-fit:cover;object-position:center}.iframe{width:100%;height:100%;display:block;margin:20px auto;border:0 none;max-width:880px;border-radius:8px;box-shadow:0 1px 1px rgba(0,0,0,0.11),0 2px 2px rgba(0,0,0,0.11),0 4px 4px rgba(0,0,0,0.11),0 6px 8px rgba(0,0,0,0.11),0 8px 16px rgba(0,0,0,0.11)}.btnArrowLeft,.btnArrowRight{position:absolute;top:50%;transform:translateY(-50%);width:24px;height:24px;background-color:#eee;display:flex;justify-content:center;align-items:center;border-radius:9999px;cursor:pointer}.btnArrowLeft:hover,.btnArrowRight:hover{background-color:#fff}.btnArrowLeft{left:10px}.btnArrowRight{right:10px}.hidden{visibility:hidden}.btnArrowDisable{display:none}.leftArrow{transform:rotate(180deg)}
